import React from 'react'
import { BtnGroups, InfoRow, ViewBody, ViewHeader } from '../../pages/community/community.style'
import Aside from '../../common/Aside/Aside'
interface Props {
  value: {
    title:string;
    writer:string;
    date:string;
    cnt:number;
  }
 }
const BoardView = ({value: {title,writer,date, cnt}} : Props) => {
  return (
    <>
      <ViewHeader>
        <h2>{title}</h2>
        <InfoRow>
          <span>{writer}</span>
          <span>{date}</span>
          <span> {cnt}</span>
        </InfoRow>
      </ViewHeader>
      <ViewBody className='board'>
        <p>. .</p>
        <p> 22 4 .^^</p>
        <p>4 1     .</p>
        <strong> </strong>
        <p>,       !</p>
        <p> , ,      !</p>
        <p>    ,   .</p>
      </ViewBody>
      <BtnGroups>
        <button></button>
        <button className='prev'></button>
        <button className='next'></button>
      </BtnGroups>
    </>
  )
}

export default BoardView