import React from 'react'
import { Link } from 'react-router-dom'
import { AllMenuStyle } from './com_layout.style'
import { Container, Flex } from '../../style/contents.style';
export interface AllMenuProps{
  isActive ?: boolean;
}
const AllMenue = ({isActive} : AllMenuProps) => {
  return (
    <>
      <AllMenuStyle className={isActive ? "on" : ""}>
        <Container pdt="0">
          <Flex alOpt="flex-start">
            <div className="col">
              <h2></h2>
              <ul>
                <li>
                  <Link to="/class"> VOD</Link>
                </li>
                <li>
                  <Link to="/class/live">Live </Link>
                </li>
              </ul>
            </div>
            <div className="col">
              <h2></h2>
              <ul>
                <li>
                  <Link to="/library"> </Link>
                </li>
                <li>
                  <Link to="/library/reportsch"> </Link>
                </li>
              </ul>
            </div>
            <div className="col">
              <h2></h2>
              <ul>
                <li>
                  <Link to="/community"> </Link>
                </li>
                <li>
                  <Link to="/community/dignos_case"></Link>
                </li>
                <li>
                  <Link to="/community/event"></Link>
                </li>
              </ul>
            </div>
          </Flex>
        </Container>
      </AllMenuStyle>
    </>
  )
}

export default AllMenue