import React, { useState } from 'react'
import { Container, Contents, Flex } from '../../style/contents.style'
import Aside from '../../common/Aside/Aside'
import PageTitle from '../../components/Layout/PageTitle'
import { Label, LabelGrp, Video, VodCont, ProList, VodTitle, ProIntroBox, FavRow, Guidebox } from './class.style'
import { RoundStyle } from '../../components/Layout/layout.style'
import ProItem from '../../components/Classroom/ProItem'
import ProfilImg from "../../images/profile_img.jpg"
import FavIco from '../../components/Layout/FavIco'

const VodView = () => {
  const [isOpen, setIsOpen] = useState(false);
  const handleToggle = () => {
    setIsOpen(!isOpen);
  };
  return (
     <Container id='content'>
      <div className='contents-inner'>
        <Contents>
          <PageTitle titleText=" VOD"/>
          <Video>
            <iframe src="https://www.youtube.com/embed/MjS4x2JOEYQ" />
          </Video>
          <div>
            <VodTitle>
              <RoundStyle className='cate'> </RoundStyle>
              <h3>() /         </h3>
              <div className="hash"><span>#</span><span>#</span><span>#</span></div>
              <LabelGrp><Label></Label><Label $mode="purple"></Label><Label $mode="skyblue"></Label></LabelGrp>
            </VodTitle>
            <VodCont>
              <li>
                <em> </em>
                <span>2022.01.26~2023.01.26</span>
              </li>
              <li>
                <em></em>
                <span className='file'><a href='#'>.pdf</a>/<a href='#'>.pdf</a></span>
              </li>
              <li>
                <em></em>
                <span> </span>
              </li>
            </VodCont>
          </div>
          <ProList>
            <ProItem />
            <ProItem />
          </ProList>
          <ProIntroBox>
            <ul className='program-list'>
              <li>
                <em className='time'>00:25</em><span> </span>
              </li>
              <li><span className="time">00:46</span><span>Idiopathic ?</span></li>
              <li><span className="time">02:36</span><span>Outflow tract of Ventricle</span></li>
              <li><span className="time">03:21</span><span>Bundle branch block pattern</span></li>
              <li><span className="time">04:10</span><span>Axis</span></li>
            </ul>
            <div>
              <img src={ProfilImg} alt="" />
            </div>
          </ProIntroBox>
          <FavRow>
            <FavIco className={isOpen ? "on" : ""}
              onClick={handleToggle}
            />
            <span className="num">15</span>
          </FavRow>
          <Guidebox>
            <strong>  ?</strong>
            <p>-    ,    .</p>
          </Guidebox>
        </Contents>
        <Aside />
      </div>
    </Container>
  )
}

export default VodView