import React from 'react'
import { SchInputStyle } from './form.style'

const SchInput = () => {
  return (
    <li style={{flex:"1"}}>
      <SchInputStyle className="sch_box">
        <input type="text" placeholder=" "></input>
        <button className="btn"></button>
      </SchInputStyle>
    </li>
  )
}

export default SchInput