import React from 'react'
import { Container, Contents } from '../../style/contents.style'
import Visual from '../../components/Layout/Visual'
import SchInput from '../../components/Form/SchInput'
import SelectBox from '../../components/Form/SchSelectBox'
import { SchGroup } from '../../components/Form/form.style'
import { BtnMore } from '../../components/Layout/layout.style'
import { CommunityTb } from './community.style'
import Aside from '../../common/Aside/Aside'
import { Link } from 'react-router-dom'
const select_list = [
  { label: '', value: 'title' },
  { label: '+', value: 'total' },
  { label: '', value: 'content' }
];
const FreeBoard = () => {
  return (
    <>
      <Container $full pdt="70px" id="content">
        <Visual 
          bgType="freeBoard" 
          title="" 
          subTitle="   ,   ."
        />
      </Container>
      <Container pdt="0">
        <div className='contents-inner'>
          <Contents>  
            <SchGroup 
              schWidth="400px" 
              mbbVal="var(--base-gap)"
              bdColor="var(--form-border)"
            >
              <SelectBox selectList={select_list}/>
              <SchInput />
            </SchGroup>
            <CommunityTb>
              <table>
                <caption>,, , ,   </caption>
                <colgroup>
                  <col style={{ width: '80px' }} />
                  <col style={{ width: 'auto' }} />
                  <col style={{ width: '185px' }} />
                  <col style={{ width: '100px' }} />
                  <col style={{ width: '80px' }} />
                </colgroup>
                <thead><tr><th scope="col"></th><th scope="col" className="align_left"></th><th scope="col"></th><th scope="col"></th><th scope="col"></th></tr></thead>
                <tbody><tr><td>215</td><td className="title"><Link to="/community/free_boardview">  </Link><span className="comm-cnt">[13]</span><i className="ico_new"></i></td><td></td><td>2022-10-04</td><td>406</td></tr><tr><td>214</td><td className="title"><Link to="/community/free_boardview">   </Link><span className="comm-cnt">[2]</span></td><td></td><td>2023-10-04</td><td>406</td></tr><tr><td>213</td><td className="title"><Link to="/community/free_boardview">  </Link></td><td></td><td>2022-10-04</td><td>406</td></tr></tbody>
              </table>
              <BtnMore></BtnMore>
            </CommunityTb>
          </Contents>
          <Aside />
        </div>
      </Container>
    </>
  )
}

export default FreeBoard