import React, { useState } from 'react'
import { Container, Flex, FlexSpace } from '../../style/contents.style'
import {Gnb, HeaderFix, Identify, Member, NaviWrap, NavList, SkipNav, Submenu } from './com_layout.style'
import { Link, useLocation } from 'react-router-dom'
import SearchBox from './SearchBox'
import AllMenue from './AllMenue'
import VetLogo from './VetLogo'
import { HashLink } from 'react-router-hash-link';
const Header = () => {
  const [isActive, setIsActive] = useState(false);
  const location = useLocation();
  const handleButtonClick = () => {
    setIsActive(!isActive);
  };
  return (
    <HeaderFix>
       <SkipNav>
          <HashLink smooth to="#content">
             
          </HashLink>
      </SkipNav>
      <Container mbb0 pdt="0">
        <FlexSpace>
          <NaviWrap>
            <VetLogo />
            <nav>
              <NavList>
                <li>
                  <Link to="/class" 
                    className={`link ${location.pathname.startsWith("/class") ? "on" : ""}`}
                  >
                    </Link>
                  <Submenu>
                    <li>
                      <Link to="/class"> VOD</Link>
                    </li>
                    <li>
                      <Link to="/class/live">Live </Link>
                    </li>
                  </Submenu>
                </li>
                <li>
                <Link to="/library" 
                  className={`link ${location.pathname.startsWith("/library") ? "on" : ""}`}
                  >
                  </Link>
                  <Submenu>
                    <li>
                      <Link to="/library"> </Link>
                    </li>
                    <li>
                      <Link to="/library/reportsch"> </Link>
                    </li>
                  </Submenu>
                </li>
                <li>
                  <Link to="/community" 
                    className={`link ${location.pathname.startsWith("/community") ? "on" : ""}`}
                  >
                    </Link>
                  <Submenu>
                    <li>
                      <Link to="/community"> </Link>
                      {/* <Link to="/community/tip">  Tip</Link> */}
                    </li>
                    <li>
                      <Link to="/community/dignos_case"></Link>
                    </li>
                    <li>
                      <Link to="/community/event"></Link>
                    </li>
                  </Submenu>
                </li>
              </NavList>
            </nav>
          </NaviWrap>
          <Flex>
            <Identify> </Identify>
            <Member>
              <button aria-label=''><span className="blind"></span> <span className='ico-alram'></span></button>
              <Submenu>
                <li><a href="#"></a></li>
                <li><a href="#"></a></li>
                <li><a href="#">1:1</a></li>
                <li><a href="#"></a></li>
                <li><a href="#"></a></li>
              </Submenu>
            </Member>
            <SearchBox />
            <Gnb onClick={handleButtonClick} className={isActive ? "on" : ""}>
              <span className="ico-menu"></span>
            </Gnb>
          </Flex>
        </FlexSpace>
        <AllMenue isActive= {isActive} />

      </Container>
    </HeaderFix>
   
  )
}

export default Header