import React, { useState } from 'react'
import { Flex } from '../../style/contents.style'
import { BtnToggle, ListInfo } from '../../pages/classroom/class.style'
import IntroImg from "../../images/doctor_sample1.png"

function ProItem() {
  const [isOpen, setIsOpen] = useState(false);
  const handleToggle = () => {
    setIsOpen(!isOpen);
  };
  return (
    <li className={isOpen ? "open" : ""}>
      <Flex>
        <figure>
          <img src={IntroImg} alt='' />
        </figure>
        <ListInfo>
          <strong> </strong>
          <span className='belong'> </span>
          <ul className="info-list">
            <li><em></em> <span className="val blue"> </span></li>
            <li><em></em> <span className="val blue">,,,,,,</span>
            <ul><li>-    </li><li>-  Editorial board: Diabetes, Obesity and Metabolism</li><li>- Massachusetts General Hospital/Harvard Medical School</li></ul></li>
          </ul>
        </ListInfo>
      </Flex>
      <BtnToggle onClick={handleToggle}  className={isOpen ? "open" : ""}></BtnToggle>
    </li>
  )
}

export default ProItem