import React, { useState } from 'react'
import { Container, FlexSpace } from '../../style/contents.style'
import { Address, FamliySite, FooterLayout, Logo, SiteLink } from './com_layout.style'
import VetLogo from './VetLogo'


const Footer = () => {
  const [isOpen, setIsOpen] = useState(false);
  const handleToggle = () => {
    setIsOpen(!isOpen);
  };
  return (
    <FooterLayout>
      <Container pdt="var(--cont-gap,0)"> 
        <FlexSpace>
          <div>
            <VetLogo />
            <SiteLink>
              <a href="#"></a>
              <a href="#"></a>
              <a href="#"></a>
              <a href="#"></a>
              <a href="#" className="point">
                
              </a>
            </SiteLink>
            <Address>
              <span>   114 12 ()</span>
              <span> : 1522-0209</span>
              <span>Fax : 02-563-8399</span>
            </Address>
            <div className="copy">
              Copyright (c)Mcircle Corp. All rights reserved.
            </div>
          </div>
          <FamliySite className={isOpen ? "open" : ""}>
            <div className={isOpen ? "sites open" : "sites"}>
              <a href="#" target="_blank">
                
              </a>
              <a href="#" target="_blank">
                
              </a>
              <a href="#" target="_blank">
                
              </a>
              <a href="#" target="_blank">
                
              </a>
              <a href="#" target="_blank">
                
              </a>
              <a href="#" target="_blank">
                
              </a>
            </div>
            <button className="site_box" onClick={handleToggle}>
              Family Site
            </button>
          </FamliySite>
        </FlexSpace>
      </Container>
    </FooterLayout> 
  )
}

export default Footer